"use client"

import { useState } from 'react'
import Layout from '@/components/shared/Layout'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Badge } from "@/components/ui/badge"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"

interface Announcement {
  id: string
  message: string
  status: 'playing' | 'scheduled' | 'completed'
  priority: 'high' | 'medium' | 'low'
  target: string
  scheduledTime?: string
  duration: string
}

export default function PASystemPage() {
  const [selectedZone, setSelectedZone] = useState<string>('all')

  const announcements: Announcement[] = [
    {
      id: "PA-001",
      message: "Attention all personnel: Shift change in 15 minutes",
      status: "playing",
      priority: "high",
      target: "All Zones",
      duration: "30s"
    },
    {
      id: "PA-002",
      message: "Loading bay 3 ready for truck arrival",
      status: "scheduled",
      priority: "medium",
      target: "Loading Zone",
      scheduledTime: "12:45 PM",
      duration: "15s"
    },
    {
      id: "PA-003",
      message: "Regular safety check reminder",
      status: "completed",
      priority: "low",
      target: "Workshop",
      duration: "45s"
    }
  ]

  const zones = [
    { id: 'all', name: 'All Zones' },
    { id: 'loading', name: 'Loading Zone' },
    { id: 'unloading', name: 'Unloading Zone' },
    { id: 'workshop', name: 'Workshop' },
    { id: 'parking', name: 'Parking Area' }
  ]

  const getStatusBadge = (status: Announcement['status']) => {
    const styles = {
      playing: 'bg-green-100 text-green-800',
      scheduled: 'bg-blue-100 text-blue-800',
      completed: 'bg-gray-100 text-gray-800'
    }
    return styles[status]
  }

  const getPriorityBadge = (priority: Announcement['priority']) => {
    const styles = {
      high: 'bg-red-100 text-red-800',
      medium: 'bg-yellow-100 text-yellow-800',
      low: 'bg-blue-100 text-blue-800'
    }
    return styles[priority]
  }

  return (
    <Layout title="PA System Control">
      <div className="space-y-6">
        {/* New Announcement Form */}
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">New Announcement</h2>
          <div className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Target Zone
                </label>
                <Select value={selectedZone} onValueChange={setSelectedZone}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select zone" />
                  </SelectTrigger>
                  <SelectContent>
                    {zones.map((zone) => (
                      <SelectItem key={zone.id} value={zone.id}>
                        {zone.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Priority Level
                </label>
                <Select defaultValue="medium">
                  <SelectTrigger>
                    <SelectValue placeholder="Select priority" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="high">High Priority</SelectItem>
                    <SelectItem value="medium">Medium Priority</SelectItem>
                    <SelectItem value="low">Low Priority</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Announcement Message
              </label>
              <Textarea 
                placeholder="Enter your announcement message here..."
                className="h-24"
              />
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Schedule Time (Optional)
                </label>
                <Input type="datetime-local" />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Duration
                </label>
                <Input type="number" placeholder="Duration in seconds" />
              </div>
            </div>

            <div className="flex justify-end space-x-2">
              <Button variant="outline">Preview</Button>
              <Button>Broadcast Now</Button>
            </div>
          </div>
        </Card>

        {/* Active and Scheduled Announcements */}
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">Announcement History</h2>
          <div className="rounded-lg border">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>ID</TableHead>
                  <TableHead>Message</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Priority</TableHead>
                  <TableHead>Target</TableHead>
                  <TableHead>Time</TableHead>
                  <TableHead>Duration</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {announcements.map((announcement) => (
                  <TableRow key={announcement.id}>
                    <TableCell className="font-medium">{announcement.id}</TableCell>
                    <TableCell className="max-w-xs truncate">
                      {announcement.message}
                    </TableCell>
                    <TableCell>
                      <Badge className={getStatusBadge(announcement.status)}>
                        {announcement.status.charAt(0).toUpperCase() + announcement.status.slice(1)}
                      </Badge>
                    </TableCell>
                    <TableCell>
                      <Badge className={getPriorityBadge(announcement.priority)}>
                        {announcement.priority.charAt(0).toUpperCase() + announcement.priority.slice(1)}
                      </Badge>
                    </TableCell>
                    <TableCell>{announcement.target}</TableCell>
                    <TableCell>{announcement.scheduledTime || 'Immediate'}</TableCell>
                    <TableCell>{announcement.duration}</TableCell>
                    <TableCell>
                      <div className="space-x-2">
                        <Button variant="ghost" size="sm">
                          {announcement.status === 'playing' ? 'Stop' : 'Play'}
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </Card>

        {/* System Status */}
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">System Status</h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            {zones.map((zone) => (
              <div key={zone.id} className="p-4 border rounded-lg">
                <div className="flex items-center justify-between">
                  <span className="font-medium">{zone.name}</span>
                  <Badge className="bg-green-100 text-green-800">Online</Badge>
                </div>
                <div className="mt-2 text-sm text-gray-600">
                  Last broadcast: 5 minutes ago
                </div>
              </div>
            ))}
          </div>
        </Card>
      </div>
    </Layout>
  )
}
